@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
/*   color variables */
/*   border radius */
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Noto Sans", sans-serif;
  color: #4f546c;
}
.wrapper {
  margin: 2rem;
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: 1fr;
  justify-content: center;
}
.card {
  box-shadow: 0 0 10px 5px #e1e5ee;
  border-radius: 0.2rem;
  min-width: 28rem;
  display: flex;
}
.card__img {
  width: 14rem;
  height: 11rem;
  object-fit: cover;
}
.card__body {
  margin: 1rem;
  flex-grow: 1;
}
.card__title {
  line-height: 1.4rem;
  margin-bottom: 0.5rem;
}
.card__description {
  line-height: 1.2rem;
}
.card__price {
  font-size: 1.4rem;
  margin-top: 0.6rem;
}
.card__price::before {
  content: "$";
  font-size: 1rem;
  position: relative;
  top: -0.3rem;
  padding-right: 0.1rem;
}
.card__btn {
  border: none;
  border-top: 1px solid #e1e5ee;
  background-color: transparent;
  font-family: inherit;
  font-size: 1rem;
  font-weight: bold;
  color: inherit;
  width: 100%;
  padding-top: 1rem;
  margin-top: 1rem;
  cursor: pointer;
}
.card__btn:hover {
  color: #0e48fe;
}
@media screen and (min-width: 600px) {
  .wrapper {
    grid-template-columns: repeat(auto-fit, minmax(14rem, 16rem));
  }
  .card {
    flex-direction: column;
    text-align: center;
    min-width: 14rem;
  }
  .card__img {
    width: 100%;
    height: 12rem;
  }
}

/* report page */
.reportsWrapper {
  display: grid;
  place-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.reportsBox {
  position: relative;
  border: 1px solid;
  width: 80%;
  min-width: 50rem;
  height: 6.5rem;
}
.orderNumber {
  float: right;
}
.orderDate {
  width: 6rem;
  height: 1rem;
  text-align: center;
  bottom: 0.25rem;
  left: -0.5rem;
  position: absolute;
}
.orderPictures {
  border: 1px solid;
  width: 40rem;
  height: 5rem;
}
.approvals {
  position: absolute;
  height: 1.3rem;
  width: 40rem;

  bottom: 0.05rem;
  right: 0rem;
}
.regApproval {
  height: 100%;
  width: 33.33%;
  float: left;
}
.vpApproval {
  height: 100%;
  width: 33.33%;
  float: right;
}
.clerkApproval {
  height: 100%;
  width: 33.33%;
  float: right;
}
.approvalTag {
background-color:  #32CA76;
border-radius: 7px;
width: 99%;
color: #FFF;
text-align: center;
font-size: 1rem;
line-height: 1.4rem;
}
.waitingTag {
background-color:  #FEEFB3;
border-radius: 7px;
width: 99%;
color: #FFF;
text-align: center;
font-size: 1rem;
line-height: 1.4rem;
}
.deniedTag {
background-color:  #D8000C;
border-radius: 7px;
width: 99%;
color: #FFF;
text-align: center;
font-size: 1rem;
line-height: 1.4rem;
}

/* place order page */
.checkout {
  position: relative;
  display: grid;
  place-items: center;
}
.checkoutCard {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "checkoutPicture checkoutTitle checkoutItemCount checkoutPrice";
}

.checkoutPrice {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    ". checkoutPriceData checkoutPriceData"
    ". . ."
    ". . .";
  grid-area: checkoutPrice;
}

.checkoutPriceData { 
  grid-area: checkoutPriceData; 
  justify-self: end;
}

.checkoutTitle { grid-area: checkoutTitle; }

.checkoutPicture { 
  grid-area: checkoutPicture;
  height: 6rem;
}

.checkoutItemCount { 
  grid-area: checkoutItemCount; 
  justify-self: end;
  align-self: center;
}

.orderCount {
  border: darkslategray solid;
  border-radius: 12%;
  text-align:center
}
.orderinput {
  max-width: 1.5rem;
  height: 1.5rem;
  text-align: center;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.minMaxButton {
background-color: #f1f1f1;
color: black;
width: 1rem;
height: 1.5rem;
display: flex;
align-items: center;
justify-content: center;
cursor: pointer;
}
.right {
  float: right
}
.left {
  float: left
}

/* messages page */
.messages {
  margin: auto;
  width: 80%;
}
.ordersList {
  margin: auto;
  width: 45rem;
}
.orders {
  margin-bottom: 1rem;
  box-shadow: 0 0 10px 5px #e1e5ee;
  border-radius: 1rem;
  padding: 0.5rem;
}
.messagesImagesDiv {
  margin-top: 1.3rem;
	max-height: 6rem;
}
.messagesImage {
	max-height: 6rem;
}

.approvalWrapper {
  display: inline-block;
  width: 40rem;
  max-height: 2rem;
  height: 1.5rem;
  text-align: center;
}
.clerk {
  display: inline-block;
  border-radius: 1rem;
}
.regional {
  display: inline-block;
  border-radius: 1rem;
}
.vp {
  display: inline-block;
  border-radius: 1rem;
}
.approved {
  height: 100%;
  width: 33.33%;
  background-color: rgb(40, 165, 40);
  color: white;
}
.denied {
  height: 100%;
    width: 33.33%;
    background-color: rgb(211, 18, 18);
    color: white;
}
.waitingApproval {
    height: 100%;
    width: 33.33%;
    background-color: rgb(197, 186, 38);
}
.messagesOrderNumber {
  float: left;
  top: 0px;
  max-width: 40rem;
  height: 0.5rem;
}
.messagesCloseOrder {
  float: right;
  top: 0rem;
  padding-right: 0.7rem;
}

.MSDS {
  margin: auto;
  width: 80%;
}

.seachbar_wrapper div {
  position: relative;
}
.seachbar_wrapper input {
	width: 100%;
	font-size: 30px;
	border-radius: 8px;
	padding: 5px;
}
.seachbar_wrapper span {
	position: absolute;
	font-size: 35px;
	right: 10px;
	cursor: pointer;
}
.navbar-wrapper {
  display: flex;
  height: 80px;
  justify-content: space-between;
  background-color: #060b26;
}

.navbar {
  display: flex;
  padding-right: 30px;
  align-items: center;
}

.menu-item-count {
	display: flex;
	width: 20px;
	height: 20px;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 26px;
	right: 33px;
	font-size: 12px;
	/* background: #fff; */
	border-radius: 50%;
	font-weight: bold;
	color: #000;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 1;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}

.shopping:hover .shopping-item {
	transform: translateY(0px);
	opacity: 1;
	visibility: visible;
}

.shopping .shopping-item {
	position: absolute;
	top: 80px;
	right: 0;
	width: 300px;
	background: #fff;
	padding: 20px 25px;
	transition: all 0.3s ease 0s;
	transform: translateY(10px);
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
	opacity: 0;
	visibility: hidden;
	z-index: 99;
}

.shopping .dropdown-cart-header {
	padding-bottom: 10px;
	margin-bottom: 15px;
	border-bottom: 1px solid #e6e6e6;
}

.shopping .dropdown-cart-header span {
	text-transform: uppercase;
	color: #222;
	font-size: 13px;
	font-weight: 600;
}

.shopping-item .bottom {
	text-align: center;
}

.shopping-item .total {
	overflow: hidden;
	display: block;
	padding-bottom: 10px;
}

.shopping-item .total span {
	text-transform: uppercase;
	color: #222;
	font-size: 13px;
	font-weight: 600;
	float: left;
}

.shopping-item .total .total-amount {
	float: right;
	font-size: 14px;
}

.shopping-item .bottom .btn {
	background: #222;
	padding: 10px 20px;
	display: block;
	color: #fff;
	margin-top: 10px;
	border-radius: 0px;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 500;
}

.shopping-list .shopping-list-item {
	display: flex;
	padding-bottom: 10px;
	margin-bottom: 10px;
	border-bottom: 1px solid #eceaea;
}

.shopping-list .shopping-list-item img {
  width: 50px;
  margin-right: 10px;
}
.shopping-list .shopping-list-item h4 {
  font-size: 16px;
}
.shopping-list .shopping-list-item p {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}
.shopping-list .shopping-list-item .remove {
  color: red;
}

